$prefix: pfe;

@import "theme";
@import "typography";

.PFElement {
    font-family: var(--#{$prefix}-theme--font-family);
    color: var(--#{$prefix}-broadcasted--color--text);
    a {
        color: var(--#{$prefix}-broadcasted--color--ui-link);
        @each $state in (hover, focus) {  // visited introduces bugs; add this later
            &:#{$state} {
                color: var(--#{$prefix}-broadcasted--color--ui-link--#{$state});
            }
        }
    }
    
    > *:first-child {
        margin-top: 0;
    }

    h1 {
        @extend %pfe-headline-1;
    }

    h2 {
        @extend %pfe-headline-2;
    }

    h3 {
        @extend %pfe-headline-3;
    }

    h4 {
        @extend %pfe-headline-4;
    }

    h5 {
        @extend %pfe-headline-5;
    }

    h6 {
        @extend %pfe-headline-6;
    }

    p {
        @extend %pfe-text;
    }

    pre, code {
        font-family: var(--#{$prefix}-theme--font-family--code, unquote(map-deep-get($font, font-family, code)));
    }
}

/* COMPONENT SPECIFIC STYLES */
#{$prefix}-cta[priority="primary"] a,
#{$prefix}-cta[priority="secondary"] a {
    font-size: 1em;
}

#{$prefix}-cta:not([priority="primary"]) a,
#{$prefix}-cta:not([priority="secondary"]) a {
    text-decoration: none;
    &:after {
        margin-bottom: .25em;
    }
}
