$logo: (
    favicon:      "url(https://static.redhat.com/libs/redhat/brand-assets/2/corp/favicon.png)",         //--pfe-theme--logo--favicon
    svg:          "url(https://static.redhat.com/libs/redhat/brand-assets/2/corp/logo.svg)",            //--pfe-theme--logo--svg
    svg--on-dark: "url(https://static.redhat.com/libs/redhat/brand-assets/2/corp/logo--on-dark.svg)",   //--pfe-theme--logo--svg--on-dark
    png:           "url(https://static.redhat.com/libs/redhat/brand-assets/2/corp/logo.png)",           //--pfe-theme--logo--png
    png--on-dark: "url(https://static.redhat.com/libs/redhat/brand-assets/2/corp/logo--on-dark.png)",   //--pfe-theme--logo--png--on-dark
);

$box-shadow: (
    sm: 0 0.0625rem 0.125rem 0 rgba(37, 37, 39, 0.1),                                                             //--pfe-theme--box-shadow--sm
    md: "0 0.125rem 0.0625rem 0.0625rem rgba(37, 37, 39, 0.1), 0 0.25rem 0.6875rem 0.375rem rgba(37, 37, 39, 0.1)", //--pfe-theme--box-shadow--md
    lg: "0 0.1875rem 0.4375rem 0.1875rem rgba(37, 37, 39, 0.1), 0 0.6875rem 1.5rem 1rem rgba(37, 37, 39, 0.1)",     //--pfe-theme--box-shadow--lg
    inset: inset 0 0 0.625rem 0 rgba(37, 37, 39, 0.1)                                                             //--pfe-theme--box-shadow--inset
);

$spacing: (
    container-spacer:   16px,  //--pfe-theme--container-spacer
    container-padding:  16px,  //--pfe-theme--container-padding
    content-spacer:     30px,  //--pfe-theme--content-spacer
);

$animation: (
    timing: .2s ease-in 0s
);