$font: (
    font-size: (
        xxl:      48px,  //--pfe-theme--font-size--xxl
        xl:       40px,  //--pfe-theme--font-size--xl
        alpha:    36px,  //--pfe-theme--font-size--alpha
        beta:     28px,  //--pfe-theme--font-size--beta
        gamma:    24px,  //--pfe-theme--font-size--gamma
        delta:    20px,  //--pfe-theme--font-size--delta
        epsilon:  18px,  //--pfe-theme--font-size--epsilon
        zeta:     16px,  //--pfe-theme--font-size--zeta
        default:  18px,  //--pfe-theme--font-size

    ),
    line-height: (
        xxl:      1.2,  //--pfe-theme--line-height--xxl
        xl:       1.2,  //--pfe-theme--line-height--xl
        alpha:    1.3,  //--pfe-theme--line-height--alpha
        beta:     1.3,  //--pfe-theme--line-height--beta
        gamma:    1.3,  //--pfe-theme--line-height--gamma
        delta:    1.5,  //--pfe-theme--line-height--delta
        epsilon:  1.5,  //--pfe-theme--line-height--epsilon
        zeta:     1.5,  //--pfe-theme--line-height--zeta
        default:  1.5,  //--pfe-theme--line-height
    ),
    font-weight: (
        xxl:      300,  //--pfe-theme--font-weight--xxl
        xl:       300,  //--pfe-theme--font-weight--xl
        alpha:    400,  //--pfe-theme--font-weight--alpha
        beta:     400,  //--pfe-theme--font-weight--beta
        gamma:    400,  //--pfe-theme--font-weight--gamma
        delta:    400,  //--pfe-theme--font-weight--delta
        epsilon:  400,  //--pfe-theme--font-weight--epsilon
        zeta:     400,  //--pfe-theme--font-weight--zeta
        default:  400,  //--pfe-theme--font-weight
    ),
    font-family: (
        default: "\"RedHatText\", \"Overpass\", Overpass, Helvetica, Arial, sans-serif",    //--pfe-theme--font-family
        heading: "\"RedHatDisplay\", \"Overpass\", Overpass, Helvetica, Arial, sans-serif", //--pfe-theme--font-family--heading
        code:    "\"Overpass Mono\", Consolas, Monaco, Andale Mono, monospace"              //--pfe-theme--font-family--code
    )
);

$color--text: (
    default:   #151515, //--pfe-theme--color--text
    dark:      #fff,    //--pfe-theme--color--text--on-dark
    saturated: #eee     //--pfe-theme--color--text--on-saturated
);

$link: (
    text-decoration: (
        default: underline, //--pfe-theme--link--text-decoration
        hover:   underline  //--pfe-theme--link--text-decoration--hover
    )
);
