$color--surface: (
    lightest: (
      background: #ffffff,  //--pfe-theme--color--surface--lightest
      text:       #333333,  //--pfe-theme--color--surface--lightest--text
      link: (
          default: #0066cc,  //--pfe-theme--color--surface--lightest--link
          visited: #0066cc,  //--pfe-theme--color--surface--lightest--link--visited
          hover:   #004080,  //--pfe-theme--color--surface--lightest--link--hover
          focus:   #004080,  //--pfe-theme--color--surface--lightest--link--focus
      )
    ),
    lighter: (
      background: #f0f0f0,  //--pfe-theme--color--surface--lighter
      text:       #333333,  //--pfe-theme--color--surface--lighter--text
      link: (
          default: #0066cc,  //--pfe-theme--color--surface--lighter--link
          visited: #0066cc,  //--pfe-theme--color--surface--lighter--link--visited
          hover:   #004080,  //--pfe-theme--color--surface--lighter--link--hover
          focus:   #004080,  //--pfe-theme--color--surface--lighter--link--focus
      )
    ),
    base: (
      background: #dddddd,  //--pfe-theme--color--surface--base
      text:       #333333,  //--pfe-theme--color--surface--base--text
      link: (
          default:#0066cc,  //--pfe-theme--color--surface--base--link
          visited:#0066cc,  //--pfe-theme--color--surface--base--link--visited
          hover:  #004080,  //--pfe-theme--color--surface--base--link--hover
          focus:  #004080,  //--pfe-theme--color--surface--base--link--focus
      )
    ),
    darker: (
      background: #444444,  //--pfe-theme--color--surface--darker
      text:       #ffffff,  //--pfe-theme--color--surface--darker--text
      link: (
          default: #73bcf7,  //--pfe-theme--color--surface--darker--link
          visited: #73bcf7,  //--pfe-theme--color--surface--darker--link--visited
          hover:   #2b9af3,  //--pfe-theme--color--surface--darker--link--hover
          focus:   #2b9af3,  //--pfe-theme--color--surface--darker--link--focus
      )
    ),
    darkest: (
      background: #1a1a1a,  //--pfe-theme--color--surface--darkest
      text:       #ffffff,  //--pfe-theme--color--surface--darkest--text
      link: (
          default:#73bcf7,  //--pfe-theme--color--surface--darkest--link
          visited:#73bcf7,  //--pfe-theme--color--surface--darkest--link--visited
          hover:  #2b9af3,  //--pfe-theme--color--surface--darkest--link--hover
          focus:  #2b9af3,  //--pfe-theme--color--surface--darkest--link--focus
      )
    ),
    complement: (
      background: #10565c,  //--pfe-theme--color--surface--complement
      text:       #ffffff,  //--pfe-theme--color--surface--complement--text
      link: (
          default:  #ffffff,  //--pfe-theme--color--surface--complement--link
          visited:  #ffffff,  //--pfe-theme--color--surface--complement--link--visited
          hover:    #d2d3d5,  //--pfe-theme--color--surface--complement--link--hover
          focus:    #d2d3d5,  //--pfe-theme--color--surface--complement--link--focus
      )
    ),
    accent: (
      background: #ee0000,  //--pfe-theme--color--surface--accent
      text:       #ffffff,  //--pfe-theme--color--surface--accent--text
      link: (
          default:  #ffffff,  //--pfe-theme--color--surface--accent--link
          visited:  #ffffff,  //--pfe-theme--color--surface--accent--link--visited
          hover:    #d2d3d5,  //--pfe-theme--color--surface--accent--link--hover
          focus:    #d2d3d5,  //--pfe-theme--color--surface--accent--link--focus
      )
    )
);

$border--surface: (
    width: 1px,     //--pfe-theme--surface--border-width
    style: solid,   //--pfe-theme--surface--border-style
    radius: 0,      //--pfe-theme--surface--border-radius
    colors: (
        default:  #d2d2d2,  //--pfe-theme--surface--border
        lightest: #ededed,  //--pfe-theme--surface--border-lightest
        darkest:  #333333,  //--pfe-theme--surface--border-darkest
    )
);